<template>
    <footer class="anterior-siguiente">
        <a href="#" @click.prevent="anterior"> Anterior </a>
        <a href="#" @click.prevent="siguiente"> Siguiente </a>
    </footer>
</template>


<script>
export default {
    name: 'AnteriorSiguiente',
    props: {
        avanzar: String
    },
    methods: {
        anterior() { this.$router.go(-1) },
        siguiente() { this.$router.push( this.$props.avanzar ) }
    }
}
</script>


<style lang="scss" scoped>
.anterior-siguiente {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 50px;
    padding-top: 10px;
    border-top: 1px solid silver;
    
    a {
        font-family: var(--fuente-titulos); 
        &:hover { color: var(--azul-primario); }
    }
}
</style>